:root {
  --dark-bg: #1E2749;
  --light-bg: #FAFAFF;
  --accent-color: #E4D9FF;
  --primary-color: #273469;
  --secondary-color: #30343F;
  --text-dark: #1E2749;
  --text-light: #FAFAFF;
  --shadow-color: rgba(0, 0, 0, 0.2);
  --border-radius: 8px;
  --font-main: 'Poppins', sans-serif;
}

body {
  font-family: var(--font-main);
  background-color: var(--light-bg);
  color: var(--text-dark);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 1.6;
}

/* Global Styles for Mobile Optimization */
@media (max-width: 768px) {
  body {
      font-size: 14px; /* Smaller font size for mobile */
  }

  h1 {
      font-size: 1.8rem; /* Responsive heading sizes */
  }

  h2 {
      font-size: 1.6rem;
  }

  h3 {
      font-size: 1.4rem;
  }

  p, .nav-links a {
      font-size: 1rem; /* Consistent text size */
  }
}
